<template><section><h1>双圆涟漪</h1>
<p><code v-pre>vc-ripple-circle-double</code> 组件用于加载双圆涟漪效果，实质是通过 <code v-pre>vc-entity</code> 和 <code v-pre>vc-graphics-ellipse</code> 按一定时间间隔加载 2 个圆，然后不停循环修改圆的半径及材质透明度实现。参考 <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a> 。</p>
<h2>示例</h2>
<h3>加载双圆涟漪效果</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-ripple-circle-double @ready="subReady" :interval="4000" imageUrl="/statics/SampleData/images/redCircle2.png" :position="position"></vc-ripple-circle-double>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu mapStyle="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-ripple-circle-double</span>
        <span class="hljs-attr">@ready</span>=<span class="hljs-string">"subReady"</span>
        <span class="hljs-attr">:interval</span>=<span class="hljs-string">"3000"</span>
        <span class="hljs-attr">imageUrl</span>=<span class="hljs-string">"/statics/SampleData/images/redCircle2.png"</span>
        <span class="hljs-attr">:position</span>=<span class="hljs-string">"position"</span>
      &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-ripple-circle-double</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-tianditu</span> <span class="hljs-attr">mapStyle</span>=<span class="hljs-string">"img_c"</span> <span class="hljs-attr">token</span>=<span class="hljs-string">"436ce7e50d27eede2f2929307e6b33c0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-tianditu</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">117.217124</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.809777</span> }
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
      },
      subReady() {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
        viewer.scene.globe.depthTestAgainstTerrain = <span class="hljs-literal">true</span>
        viewer.zoomTo(viewer.entities)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> 指定双圆涟漪效果实体添加的位置。结构：{ lng: number, lat: number, height: number } 或者 Cesium.Cartesian3</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>undefined</code></td>
<td><code v-pre>optional</code> 指定双圆涟漪效果椭圆高度。单位米。</td>
</tr>
<tr>
<td>minRadius</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定双圆涟漪效果最小半径。单位米。</td>
</tr>
<tr>
<td>maxRadius</td>
<td>Number</td>
<td><code v-pre>3000</code></td>
<td><code v-pre>optional</code> 指定双圆涟漪效果最大半径。单位米。</td>
</tr>
<tr>
<td>deviationRadius</td>
<td>Number</td>
<td><code v-pre>20</code></td>
<td><code v-pre>optional</code> 双圆半径改变大小的差值，值越大速度越快。</td>
</tr>
<tr>
<td>interval</td>
<td>Number</td>
<td><code v-pre>3000</code></td>
<td><code v-pre>optional</code> 两个圆的时间间隔，单位秒。</td>
</tr>
<tr>
<td>imageUrl</td>
<td>String</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code> 指定用于表达双圆涟漪效果的图片。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例, 以及实体数组。</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data() {
        return {
          position: { lng: 117.217124, lat: 31.809777 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
        },
        subReady(aa) {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.zoomTo(viewer.entities)
        }
      }
    }
  </script>